









































import { Component, Vue } from "vue-property-decorator"
import { Unescaper } from "./services/Unescaper"

@Component({
  components: {}
})
export default class App extends Vue {
  inputText: string = ""
  escapedText: string = ""

  inputChanged() {
    this.escapedText = Unescaper.unescape(this.inputText)
  }
}
